export const chooseImage = function (canvasId) {
  return new Promise((resolve, reject) => {
    uni.chooseImage({
      count: 1, //默认9
      sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
      success: async (res) => {
        const fileName = res.tempFiles[0].name
        const fileSize = res.tempFiles[0].size
        console.log('fileName:', res)
        const url = res.tempFiles[0].path
        mpImageCompress.clearTempImg()
        let fileInfo = await mpImageCompress.set(url, 1024 * 2, true)
        let filePath = fileInfo.filePath
        resolve({ file: filePath, status: true })
      },
    })
  })
}

export const formatRichText = function (html, tip = '') {
  html = html ? html : ''
  let newContent = ''
  newContent = html.replace(/<img[^>]*>/gi, function (match) {
    match = match.replace(`style=""`, '')
    match = match.replace(/style="[^"]+"/gi, '').replace(/style='[^']+'/gi, '')
    match = match.replace(/width="[^"]+"/gi, '').replace(/width='[^']+'/gi, '')
    match = match.replace(/height="[^"]+"/gi, '').replace(/height='[^']+'/gi, '')
    return match
  })
  newContent = newContent.replace(/style="[^"]+"/gi, function (match) {
    match = match
      .replace(/width:[^;]+;/gi, 'max-width:100%;')
      .replace(/width:[^;]+;/gi, 'max-width:100%;')
    return match
  })
  newContent = newContent.replace(/<br[^>]*\/>/gi, '')
  newContent = newContent.replace(
    /\<img/gi,
    '<img style="max-width:100%;height:auto;display:inline-block;margin:10rpx auto;"',
  )
  if (!newContent) {
    return tip
  }
  return newContent
}

// 获取网络地址参数
export function parseQueryParams(queryString) {
  if (queryString.indexOf('?') !== -1) {
    queryString = queryString.split('?')[1]
  }
  var params = {}
  if (!queryString) {
    return params
  }
  var pairs = queryString.split('&')
  for (var i = 0; i < pairs.length; i++) {
    var pair = pairs[i].split('=')
    var name = decodeURIComponent(pair[0])
    var value = pair.length > 1 ? decodeURIComponent(pair[1]) : ''
    params[name] = value
  }
  return params
}

// 对象转字符串
export function objectToQueryString(obj) {
  const params: any = Object.entries(obj)
  const encodedParams = params.map(
    ([key, value]) => `${encodeURIComponent(key)}=${encodeURIComponent(value)}`,
  )
  return encodedParams.join('&')
}
